import React from 'react'
import { Container } from "./TodosElements"
import { Layout } from "../Layout/Index"
import { Row, Col } from "react-bootstrap"
import { DataTodos } from "./Data"



export const Todos = () => {

    const secondImage = () => {

    }

    return (
        <Container>
            <Layout>
                <Row>
                    {DataTodos.map((item) => {
                        return (
                            <Col xs={6} sm={6} md={4} className="coluna-produto">

                                <div className="box-img">
                                    <img src={item.img} alt="Vestido Loja Dona Flor"
                                        onMouseOver={e => (e.currentTarget.src = item.img2)}
                                        onMouseOut={e => (e.currentTarget.src = item.img)}
                                    />
                                </div>

                                <div className="txt-new">
                                    <p>novidades</p>
                                </div>

                                <div className="info-product">
                                    <h5>{item.ref}</h5>
                                    <h6>R$ {(item.valor).toFixed(2)}</h6>
                                    <p>{item.valor_promocao}</p>
                                </div>

                            </Col>
                        )
                    })}
                </Row>
            </Layout>

        </Container>
    )
}
