import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    background: ${props => props.theme.ternary_color};
    padding: 50px 0;

    .coluna-produto{
        cursor: pointer;
        margin: 10px auto;
        text-align: center;
        color: ${props => props.theme.secundary_color};

        .box-img{
            display: block;
            width: 100%;
            height: 300px;

            @media (min-width: 450px){
                height: 450px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                position: relative;
                border-radius: 15px;
            }
        }

        .txt-new{
            width: fit-content;
            height: 15px;
            position: absolute;
            top: 10px;
            right: 30px;
            background: ${props => props.theme.ternary_color};
            
            @media (min-width: 450px){
                height: 25px;
                }

            p{
                display: block;
                font-size: clamp(.4rem, 2.5vw, 1rem);
                text-transform: uppercase;
                padding: 2px 5px 0 5px;
                font-weight: bold;

                @media (min-width: 550px){
                    padding: 2px 5px 0 5px;
                }
            }
        }

        .info-product{
            width: 90%;
            border-radius: 15px;
            margin: 0 auto;
            text-align: center;
            

            h5{
                font-size: clamp(1rem, 2.5vw, 1rem);
                margin-top: 15px;
            }
            h6{
                font-size: clamp(1.2rem, 2.5vw, 1.3rem);
            }
            p{
                font-size: clamp(1rem, 2.5vw, 1rem);
            }
        }
    }
`
