import React, {useState} from 'react'
import {Container} from './FreteElements'
import {FaTruckMoving} from "react-icons/fa"

export const FreteBanner = () => {

    const [color, setColor] = useState(false);

    setInterval(function () { 

        var p = document.querySelector(".p-color");
        var icone = document.querySelector(".icone-delivery");
        var titulo = document.querySelector(".h1");

        if(color === false){

            p.style.color="#fff" 
            icone.style.color="#fff" 
            setColor(!color);

        }else{

            p.style.color="#ffff00";
            icone.style.color="#ffff00";
            setColor(!color);

        }
      }, 2000);

    return (
        <Container>
            
            <h1 className="h1"><FaTruckMoving className="icone-delivery"/> Frete Grátis</h1>
            <p className='p-color'>Válido para compras acima de R$ 49,99. Consulte o regulamento.</p>
        </Container>
    )
}
