import React from 'react'
import {
    Container,
    BoxTxt,
    Form
} from "./ContatoElements.js"

export const Contato = () => {
    return (
        <Container id="container-contato">
            <BoxTxt>
                <h1>Contato</h1>
                <p>Entre em contato conosco!</p>

                <Form>
                    <input type="text" name="name" id="name" placeholder="Qual é o seu nome?"/>
                    <input type="text" name="name" id="name" placeholder="Qual é o seu endereço?"/>
                    <input type="text" name="name" id="name" placeholder="Telefone"/>
                    <textarea name="message" id="" cols="30" rows="10" placeholder="Como eu posso ajudar?"></textarea>
                    <button type="submit">Enviar</button>
                </Form>
            </BoxTxt>
        
        </Container>
    )
}
