import React, { useState } from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { Container } from "./SliderOwlElements.js"
import { DataTodos } from './Data'
import { AiOutlineHeart, AiFillHeart } from "react-icons/ai"
import { Layout } from "../Layout/Index"

const options = {
    margin: 10,
    responsiveClass: true,
    nav: true,
    dots: true,
    autoplay: false,
    smartSpeed: 1000,
    responsive: {
        200: {
            items: 1,
        },
        500: {
            items: 3,
        },
        1200: {
            items: 4,
        }

    },
};

export const Ofertas = () => {

    const [isFavorite, setIsFavorite] = useState(false);

    const addFavorite = () => {
        setIsFavorite(!isFavorite);
    }

    return (
        <>
            <Container>
                <Layout>
                    <h1>Melhores Ofertas</h1>
                    <hr />
                    <OwlCarousel style={{ marginTop: "20px" }} className="owl-theme" loop {...options}>
                        {DataTodos.map((item) => {
                            return (
                                <div className="item">
                                    <div className="box-img">
                                        <img src={item.img} alt="ofertas vestidos" />

                                        {isFavorite === false ?
                                            <AiOutlineHeart className="icone-favorito" onClick={() => addFavorite()} />
                                            : <AiFillHeart className="icone-favorito" style={{ color: "#FF266A" }} />
                                        }


                                    </div>

                                    <div className="txt-box">
                                        <h6>{item.ref}</h6>
                                        <h5>R$ {item.valor.toFixed(2)}</h5>
                                        <p>{item.valor_promocao}</p>
                                    </div>
                                </div>
                            )
                        })}
                    </OwlCarousel>
                </Layout>
            </Container >

            <Container>
                <Layout>
                    <h1>Novidades que chegaram para você</h1>
                    <hr />
                    <OwlCarousel style={{ marginTop: "20px" }} className="owl-theme" loop {...options}>
                        {DataTodos.map((item) => {
                            return (
                                <div className="item">
                                    <div className="box-img">
                                        <img src={item.img} alt="ofertas vestidos" />

                                        {isFavorite === false ?
                                            <AiOutlineHeart className="icone-favorito" onClick={() => addFavorite()} />
                                            : <AiFillHeart className="icone-favorito" style={{ color: "#FF266A" }} />
                                        }


                                    </div>

                                    <div className="txt-box">
                                        <h6>{item.ref}</h6>
                                        <h5>R$ {item.valor.toFixed(2)}</h5>
                                        <p>{item.valor_promocao}</p>
                                    </div>
                                </div>
                            )
                        })}
                    </OwlCarousel>
                </Layout>
            </Container >



            <Container>
                <Layout>
                    <h1>Mais queridos</h1>
                    <hr />
                    <OwlCarousel style={{ marginTop: "20px" }} className="owl-theme" loop {...options}>
                        {DataTodos.map((item) => {
                            return (
                                <div className="item">
                                    <div className="box-img">
                                        <img src={item.img} alt="ofertas vestidos" />

                                        {isFavorite === false ?
                                            <AiOutlineHeart className="icone-favorito" onClick={() => addFavorite()} />
                                            : <AiFillHeart className="icone-favorito" style={{ color: "#FF266A" }} />
                                        }


                                    </div>

                                    <div className="txt-box">
                                        <h6>{item.ref}</h6>
                                        <h5>R$ {item.valor.toFixed(2)}</h5>
                                        <p>{item.valor_promocao}</p>
                                    </div>
                                </div>
                            )
                        })}
                    </OwlCarousel>
                </Layout>
            </Container >

        </>

    )
}